.header__logoImage {
    height:60px;
    width:90px;
    border-radius: 50px;
    padding:8px 8px;
    margin-left: 35%;
}

.navbar__link--active {
    background-color:#dbf6e9;
    border-radius: 25%;
    box-shadow:5px 5px 5px 5px #9ddfd3;
} 
