@media screen and (max-width:992px) {
    #items {
        display: flex;
        flex-direction: column;
    }
    #navbar-toggler-icon {
        margin-top: -70px;
        margin-left: 91%;
    }
    #element1 {
        flex: 1;
        position: static;
        width: 59px;
        margin-left: 91%;
